    </div>
    <div class="box_four">
      <b> </b>
      <input
        type="date"
        name="bday"
        max="2023-06-01"
        min="1903-01-01"
        v-model="birdate"
        @blur="blur(3)"
      />
      <span :class="birdateflag == false ? '' : 'x_span'"
        ></span
      >
    </div>
    <div class="box_five">
      <b>&emsp;&emsp;</b>
      <select
        v-model="sheng_index"
        @change="
          shi_index = 0;
          xian = 0;
        "
      >
        <option v-for="(item, index) in citys" :key="index" :value="index">
          {{ item.name }}
        </option>
      </select>
      <select v-model="shi_index" @change="xian = 0">
        <option
          v-for="(item, index) in citys[sheng_index].city"
          :key="index"
          :value="index"
        >
          {{ item.name }}
        </option>
      </select>
      <select v-model="xian">
        <option
          v-for="(item, index) in citys[sheng_index].city[shi_index].area"
          :key="index"
          :value="index"
        >
          {{ item }}
        </option>
      </select>
    </div>
    <div class="box_six">
      <b>&emsp;</b>
      <input
        v-model="iphone"
        @blur="blur(5)"
        type="text"
        placeholder="+86"
      />
      <span
        :class="iphoneflag == true ? 'zq_span' : 'x_span'"
        v-show="iphonespanflag == false ? false : true"
        v-html="iphoneflag == true ? '' : '111'"
      ></span>
    </div>
    <div class="box_seven">
      <b></b>
      <input v-model="email" @blur="blur(6)" type="text" />
      <span
        :class="emailflag == true ? 'zq_span' : 'x_span'"
        v-show="emailspanflag == false ? false : true"
        v-html="emailflag == true ? '' : ''"
      ></span>
    </div>
    <div class="box_eight">
      <b></b>
      <input
        v-model="boardate"
        name="bday"
        max="2023-06-01"
        min="1903-01-01"
        @blur="blur(7)"
        type="date"
      />
      <span :class="boardateflag == false ? '' : 'x_span'"
        ></span
      >
    </div>
    <div class="box_ten">
      <b></b>
      <textarea
        @blur="blur(8)"
        cols="55"
        rows="10"
        v-model="content"
        placeholder=""
      ></textarea>
      <span :class="contentflag == false ? '' : 'x_span'"></span>
    </div>
    <button @click="submitFun"></button>
  </div>
