<script>
import citys from "../assets/city(1)";
import axios from "axios";
import md5 from "js-md5";
const url = "https://www.zzgoodqc.cn/index.php/index/callcenter/getheaders";
const strheader = "header";
const name = "name";
const role = "role";
const str = "ppusht123";
const sign = md5(name + role + str + strheader);
axios
  .post(
    url,
    { name, role, sign },
    {
      headers: {
        strheader: strheader,
      },
    }
  )
  .then((response) => {
    console.log("", response.data);
  })
  .catch((error) => {
    console.error(error);
  });
export default {
  data() {
    return {
      name: "",
      imgsrc:
        "https://img1.baidu.com/it/u=1388260926,2383571603&fm=253&app=138&size=w931&n=0&f=JPEG&fmt=auto?sec=1695142800&t=996956cb45dfc0f2fb5eb50e8c63cd3d",
      nick: "242422",
      sex: "1",
      birdate: "",
      address: "",
      iphone: "15187482233",
      email: "",
      boardate: "",
      content: "",
      userid: 0,
      //
      nameflag: false,
      nickflag: false,
      sexflag: false,
      birdateflag: false,
      iphoneflag: false,
      emailflag: false,
      boardateflag: false,
      contentflag: false,
      //span
      //span
      spanflag: false,
      nameinputflag: false,
      //span
      nickspanflag: false,
      //
      iphonespanflag: false,
      //
      emailspanflag: false,
      //
      citys: citys,
      selectData: "",
      //
      sheng_index: 0,
      //
      shi_index: 0,
      xian: 0,
      //
      submitflag: false,
      obj: {},
      //
      xx_box_flag: false,
      imggflag: false,
      url: "https://www.zzgoodqc.cn",
    };
  },
  mounted() {
    // this.axios.post(
    //   "https://www.zzgoodqc.cn/index.php/index/upload/uploadimg",
    //   { imgurl: "" }
    // );
  },
  methods: {
    handleFileUpload() {
      const input = document.createElement("input");
      input.type = "file";
      input.accept = "image/*";
      input.addEventListener("change", (event) => {
        const file = event.target.files[0];
        this.file = file; // 
      });
      input.click(); // 
    },
    uploadFile() {
      if (!this.imggflag) {
        if (!this.file) {
          console.log("");
          return;
        }
        const reader = new FileReader();
        reader.onload = (event) => {
          const base64 = event.target.result; // base64
          this.imgsrc = base64;
          this.file = false;
          this.axios
            .post(this.url + "/index.php/index/upload/uploadimg", {
              imgurl: base64,
            })
            .then((res) => {
              this.imgsrc = this.url + res.data.data.url;
              console.log(this.imgsrc);
            });

          // base64
        };
        reader.readAsDataURL(this.file); // DataURL
      } else {
        this.handleFileUpload(event);
      }
    },
    blur(n) {
      //
      if (n == 0) {
        if (this.name != "") {
          if (/^(?:[\u4e00-\u9fa5]{2,4})$/.test(this.name)) {
            this.nameflag = true;
            this.spanflag = true;
            this.submitflag = true;
          } else {
            this.nameflag = false;
            this.spanflag = true;
            this.submitflag = false;
          }
        }
      }
      //
      if (n == 1) {
        if (this.nick != "") {
          if (/^\w{6,12}$/.test(this.nick)) {
            this.nickflag = true;
            this.nickspanflag = true;
            this.submitflag = true;
          } else {
            this.nickflag = false;
            this.nickspanflag = true;
            this.submitflag = false;
          }
        }
      }
      //
      if (n == 2) {
        if (this.sexflag != "") {
          this.sexflag = false;
          this.submitflag = true;
        }
      }
      //
      if (n == 3) {
        if (this.birdate != "") {
          this.birdateflag = false;
          this.submitflag = true;
        }
      }
      //
      if (n == 5) {
        if (this.iphone != "") {
          if (/^1[\d]{10}$/.test(this.iphone)) {
            this.iphoneflag = true;
            this.iphonespanflag = true;
            this.submitflag = true;
          } else {
            this.iphoneflag = false;
            this.iphonespanflag = true;
            this.submitflag = false;
          }
        }
      }
      //
      if (n == 6) {
        if (this.email != "") {
          if (!/((?=@qq.com)|(?=@163.com)){1}/.test(this.email)) {
            this.emailflag = false;
            this.emailspanflag = true;
            this.submitflag = true;
          } else {
            this.emailflag = true;
            this.emailspanflag = true;
            this.submitflag = false;
          }
        }
      }
      //
      if (n == 7) {
        if (this.boardate != "") {
          this.boardateflag = false;
          this.submitflag = true;
        }
      }
      // 
      if (n == 8) {
        if (this.content != "") {
          this.contentflag = false;
          this.submitflag = true;
        }
      }
    },
    //
    submitFun() {
      if (this.name == "") {
        this.nameflag = false;
        this.spanflag = true;
        this.submitflag = false;
      }
      if (this.nick == "") {
        this.nickflag = false;
        this.nickspanflag = true;
        this.submitflag = false;
      }
      if (this.sex == "") {
        this.submitflag = false;
        this.sexflag = true;
      }
      if (this.birdate == "") {
        this.submitflag = false;
        this.birdateflag = true;
      }
      if (this.iphone == "") {
        this.submitflag = false;
        this.iphoneflag = false;
        this.iphonespanflag = true;
      }
      if (this.email == "") {
        this.submitflag = false;
        this.emailflag = false;
        this.emailspanflag = true;
      }
      if (this.boardate == "") {
        this.submitflag = false;
        this.boardateflag = true;
      }
      if (this.content == "") {
        this.submitflag = false;
        this.contentflag = true;
      }
      if (this.submitflag) {
        let data = {
          name: this.name,
          userid: 1,
          img: this.imgsrc,
          sex: this.sex == "" ? 0 : "",
          nick: this.nick,
          birthdate: this.birdate,
          iphone: this.iphone,
          emails: this.email,
          indate: this.boardate,
          mydes: this.content,
        };
        this.axios
          .post("https://www.zzgoodqc.cn/index.php/index/index/mycenter", data)
          .then((res) => {
            console.log(res);
          });
      }
    },
  },
};
</script>
